export default function IPAChart() {
    return <div>
        <h1>(Re-arranged) IPA Chart</h1>
        <PulmonicChart />
        <VowelChart />
        <NonPulmonicChart />
    </div>;
}

function IPAEntry({value, colSpan, rowSpan}) {
    // TODO: show key sequence on hover
    return <td colSpan={colSpan} rowSpan={rowSpan}>{value}</td>;
}

